import React, { Component } from 'react'
import Avatar from "../images/avtarpic.jpg"
import {BrowserRouter as Router ,Route ,  NavLink,Switch} from 'react-router-dom'
import Home from './Home'
import Particles from 'react-particles-js';
import Am from './Am';
import Do from './Do';
import Contact from './Contact';
import Page404 from './Page404';
export default class Main extends Component {
    state={
        isExpand: true
    }
    componentDidMount(){
        if(window.innerWidth <= 1100){
            this.setState({isExpand : false})
        }
        
        
    }
    render() {
        return (
            <div>
                <Router basename="/My-Portfolio">
                    <div>
                        {(this.state.isExpand)? <div>
                            <div className={`sideBar ${this.state.isExpand ? "":"sideBar-hidden "} `} >
                                <div className="container-fluid">
                                    <div className="row justify-content-around">
                                        <div style={{transition:" .5s all",width : "141px"}} className=" avtar-div  ">
                                            <img className="img-thumbnail avtar" src={Avatar} alt="avtar" />
                                            <h3 className="text-center w-100 text-white">It'<span className="text-primary">Z</span> Abhi</h3>
                                        </div>
                                    </div>
                                    <div style={{transition:" .5s all"}} className="row justify-content-center">
                                        <div className="col-md-12  mt-1  padd-0  ">
                                            <ul className="list-group ">
                                                <NavLink exact to="/" activeClassName="active" className="list-group-item  listItem">Home &nbsp; <i className="ml-3 mr-3  fas fa-house-damage fa-2x"></i></NavLink>
                                                <NavLink to="/i" className="list-group-item listItem">Who Am I ? &nbsp; <i className="ml-3 mr-3 fa-2x fas fa-user-secret"></i></NavLink>
                                                <NavLink to="/do" className="list-group-item listItem">What I Do ?&nbsp; <i className="ml-3 mr-3 fa-2x fas fa-briefcase"></i></NavLink>
                                                <NavLink to="/contact" className="list-group-item listItem">Contact Me &nbsp;<i className=" ml-3 mr-3 fa-2x fas fa-headset"></i> </NavLink>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="row justify-content-end">
                                        <div className="col-md-12 collapse-bar   ">
                                            <i onClick={()=>this.setState({isExpand : !this.state.isExpand})} className="fas fa-3x text-white fa-chevron-left"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        : 
                        <div>
                            <div  className={`sideBar ${this.state.isExpand ? "":"sideBar-hidden "} `}>
                                <div className="container-fluid">
                                    <div className="row justify-content-end">
                                        <div style={{transition:" .5s all"}} className=" avtar-div  ">
                                            <img  className="img-thumbnail avtar" src={Avatar} alt="avtar" />
                                            
                                            <h6 className="text-center mt-1   text-white">It'<span className="text-primary">Z</span> Abhi</h6>
                                        </div>
                                    </div>
                                    <div style={{transition:" .5s all"}} className="row justify-content-center ">
                                        <div className="col-md-12  mt-2 mb-2 padd-0  ">
                                            <ul className="list-group ">
                                                <NavLink exact to="/" activeClassName="active" className="list-group-item  listItem"><i className="ml-3 mr-3  fas fa-house-damage fa-2x"></i></NavLink>
                                                <NavLink to="/i" className="list-group-item listItem">  <i className="ml-3 mr-3 fa-2x fas fa-user-secret"></i></NavLink>
                                                <NavLink to="/do" className="list-group-item listItem"><i className="ml-3 mr-3 fa-2x fas fa-briefcase"></i></NavLink>
                                                <NavLink to="/contact" className="list-group-item listItem"><i className=" ml-3 mr-3 fa-2x fas fa-headset"></i> </NavLink>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="row justify-content-end">
                                        <div className="col-md-12 collapse-bar   ">
                                            <i onClick={()=>this.setState({isExpand : !this.state.isExpand})} className="fas fa-3x text-white fa-chevron-right"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>}
                        
                        <div className={`mainBar ${this.state.isExpand ? "":"mainBar-hidden "} `}>
                            <Particles params={particleObject} className="particle-class" />
                          <Switch>
                            <Route exact path='/' component={Home}/>
                            <Route  path='/i' component={Am}/>
                            <Route  path='/do' component={Do}/>
                            <Route  path='/contact' component={Contact}/>
                            <Route  component={Page404}/>
                          </Switch>
                        </div>
                        {(this.state.isExpand)? 
                            <div className=" bg-dark socialBar text-center">
                                <a href><i className="fab fa-facebook fa-2x"></i> &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp; </a>
                                <a href> <i className="fab fa-linkedin fa-2x"></i> &nbsp;&nbsp;&nbsp; |&nbsp;&nbsp;&nbsp; </a>
                                <a href><i className="fab fa-github fa-2x" ></i></a>
                            </div>
                        :
                            <div className="  socialBar ">
                                <a href><i className="fab fa-facebook fa-2x"></i> &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp; </a>
                                <a href> <i className="fab fa-linkedin fa-2x"></i> &nbsp;&nbsp;&nbsp; |&nbsp;&nbsp;&nbsp; </a>
                                <a href><i className="fab fa-github fa-2x" ></i></a>
                            </div>
                        }
                    </div>
                </Router>

            </div>
        )
    }
}
const particleObject = {
    "particles": {
      "number": {
        "value": 50,
        "density": {
          "enable": true,
          "value_area": 800
        }
      },
      "color": {
        "value": "#ffffff"
      },
      "shape": {
        "type": "circle",
        "stroke": {
          "width": 0,
          "color": "#000000"
        },
        "polygon": {
          "nb_sides": 5
        },
        "image": {
          "src": "img/github.svg",
          "width": 100,
          "height": 100
        }
      },
      "opacity": {
        "value": 0.5,
        "random": false,
        "anim": {
          "enable": false,
          "speed": 1,
          "opacity_min": 0.1,
          "sync": false
        }
      },
      "size": {
        "value": 1,
        "random": true,
        "anim": {
          "enable": false,
          "speed": 46.288401101176675,
          "size_min": 0.1,
          "sync": false
        }
      },
      "line_linked": {
        "enable": true,
        "distance": 192.40944730386272,
        "color": "#ff0000",
        "opacity": 0.4,
        "width": 1.3
      },
      "move": {
        "enable": true,
        "speed": 4.810236182596568,
        "direction": "none",
        "random": false,
        "straight": false,
        "out_mode": "out",
        "bounce": false,
        "attract": {
          "enable": false,
          "rotateX": 600,
          "rotateY": 1200
        }
      }
    },
    "interactivity": {
      "detect_on": "canvas",
      "events": {
        "onhover": {
          "enable": true,
          "mode": "grab"
        },
        "onclick": {
          "enable": true,
          "mode": "push"
        },
        "resize": true
      },
      "modes": {
        "grab": {
          "distance": 170.53621458328246,
          "line_linked": {
            "opacity": 1
          }
        },
        "bubble": {
          "distance": 400,
          "size": 40,
          "duration": 2,
          "opacity": 8,
          "speed": 3
        },
        "repulse": {
          "distance": 200,
          "duration": 0.4
        },
        "push": {
          "particles_nb": 4
        },
        "remove": {
          "particles_nb": 2
        }
      }
    },
    "retina_detect": true
  }